* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none; }

p {
  font-family: 'PT Sans';
  color: #000; }

a {
  font-family: 'PT Sans';
  color: #000;
  text-decoration: none; }

.arrows {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-between;
  top: 42%;
  transform: translate(0, -50%);
  height: 0; }
  .arrows__arrow {
    display: block;
    cursor: pointer;
    padding: 8px;
    color: #fff;
    font-size: 18px;
    margin: 0 37px; }
  .arrows__prev {
    transform: rotate(180deg); }

.container {
  max-width: 1138px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 30px; }
  .container > * {
    grid-column: span 12; }

body {
  overflow-x: hidden; }

section {
  padding: 30px 0; }

.menu {
  display: flex;
  align-items: center;
  overflow-x: scroll;
  overflow-y: hidden;
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .menu::-webkit-scrollbar {
    width: none;
    height: 0; }
  .menu__link {
    margin: 0 24px 0 0;
    line-height: 15px;
    transition: all .1s ease;
    white-space: nowrap; }
    .menu__link:hover {
      color: #4541FE; }
    .menu__link:last-of-type {
      margin: 0; }

.header {
  width: 100%;
  position: absolute;
  padding: 7px 0;
  z-index: 5; }
  .header._dark {
    background: linear-gradient(269.92deg, rgba(0, 0, 0, 0) 0.07%, #0C0C0C 99.93%); }
    .header._dark a {
      color: #fff; }
    .header._dark .menu__link {
      color: #fff; }
      .header._dark .menu__link:hover {
        opacity: 0.5; }
  .header__logo {
    font-weight: 700;
    font-size: 24px;
    text-decoration: none;
    color: #000;
    grid-column: span 1;
    align-self: center; }
  .header__menu {
    grid-column: span 8;
    align-self: center; }
  .header__cart {
    grid-column: span 3;
    align-self: center;
    justify-self: end;
    line-height: 15px;
    transition: all .1s ease;
    display: flex; }
    .header__cart:hover .header__cart-span {
      color: #4541FE; }
    .header__cart-figure {
      position: relative;
      margin: 0 10px 0 0; }
      .header__cart-figure::after {
        content: '3';
        font-size: 13px;
        color: #fff;
        position: absolute;
        background: #4541FE;
        top: 0;
        right: 0;
        transform: translate(50%, -50%);
        width: 17px;
        height: 17px;
        padding: 1px;
        text-align: center;
        border-radius: 50%; }

.footer {
  background-color: #000;
  padding: 32px 0; }
  .footer__item {
    grid-column: span 2; }
  .footer__logo {
    font-weight: 700;
    font-size: 18px;
    color: #fff; }
  .footer__list {
    margin: 12px 10px 24px 10px; }
  .footer__link {
    display: block;
    color: #fff;
    text-decoration: underline;
    margin-bottom: 8px;
    transition: all .1s ease; }
    .footer__link:hover {
      color: #666; }
    .footer__link_big {
      font-weight: 700;
      font-size: 18px; }
    .footer__link_small {
      font-size: 13px;
      color: #666; }
      .footer__link_small:hover {
        color: #fff; }
  .footer__tel {
    font-weight: 700;
    font-style: 16px;
    color: #fff;
    margin-bottom: 4px;
    white-space: nowrap; }
  .footer__p_small {
    color: #fff;
    font-size: 13px; }
  .footer__p_light {
    color: #666; }
  .footer__info {
    display: flex;
    margin: 0 -12px; }
    .footer__info > * {
      margin: 0 12px; }

.mainscreen {
  padding: 0; }
  .mainscreen__slide {
    height: 600px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: flex-end; }
    .mainscreen__slide._dark .mainscreen__title {
      color: #fff; }
  .mainscreen__content {
    padding: 72px 0;
    height: 600px;
    max-width: 1138px;
    margin: 0 auto;
    display: flex;
    align-items: flex-end; }
  .mainscreen__title {
    max-width: 500px;
    font-weight: 700;
    font-size: 64px;
    text-decoration: underline;
    line-height: 82px;
    transition: all .3s ease; }
    .mainscreen__title:hover {
      transform: translate(10px, 0); }

.mini-catalog {
  margin-bottom: 50px; }
  .mini-catalog__info {
    grid-column: span 3;
    margin: 0 30px 0 0; }
  .mini-catalog__wrapper {
    grid-column: span 9;
    display: flex;
    justify-content: space-between;
    margin: 0 -10px; }
  .mini-catalog__item {
    margin: 0 10px; }
  .mini-catalog__p {
    margin-bottom: 20px;
    font-size: 18px; }
  .mini-catalog__list {
    margin: 12px 10px 24px 10px; }
  .mini-catalog__link {
    display: block;
    color: #4541FE;
    text-decoration: underline;
    margin-bottom: 8px; }
    .mini-catalog__link:hover {
      text-decoration: none; }
    .mini-catalog__link_big {
      font-weight: 700;
      font-size: 18px; }
  .mini-catalog ._col-5 {
    grid-column: span 5; }
  .mini-catalog ._col-7 {
    grid-column: span 7;
    grid-row: span 2; }
  .mini-catalog ._col-12 {
    grid-column: span 12; }

.grid {
  padding: 24px 24px; }
  .grid:hover .grid__title {
    transform: translate(20px, 0); }
  .grid:hover .grid__p {
    transform: translate(10px, 0); }
  .grid:hover .grid__img {
    transform: rotate(-5deg); }
  .grid._dark .grid__p,
  .grid._dark .grid__title {
    color: #fff; }
  .grid._red {
    background-color: #FFA88C; }
    .grid._red .grid__img {
      margin-top: -40px;
      margin-bottom: -50px; }
  .grid._blue {
    background: #7482FF; }
  .grid._pink {
    background: #FFE9FD; }
    .grid._pink .grid__img {
      margin-top: -80px;
      margin-bottom: -80px; }
  .grid._grey {
    min-height: 480px;
    position: relative;
    background-color: #f2f2f2;
    overflow: hidden; }
    .grid._grey::after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      transition: all .3s ease;
      background: url("../assets/img/products/image-1.jpg") -28% 100% no-repeat, url("../assets/img/products/image-2.jpg") 50% 100% no-repeat, url("../assets/img/products/image-3.jpg") 125% 100% no-repeat, #f2f2f2; }
    .grid._grey:hover::after {
      transform: rotate(2deg); }
  .grid__title {
    font-weight: 700;
    font-size: 32px;
    text-decoration: underline;
    position: relative;
    z-index: 5;
    margin-bottom: 8px;
    transition: all .3s ease; }
  .grid__p {
    width: 70%;
    position: relative;
    z-index: 5;
    transition: all .3s ease; }
  .grid__img {
    display: block;
    object-fit: contain;
    transition: all .3s ease;
    position: relative;
    z-index: 3;
    width: 115%; }

.breadcrumb {
  padding: 77px 0 20px;
  position: relative; }
  .breadcrumb::after {
    content: '';
    width: 100%;
    height: 130%;
    position: absolute;
    background-color: #000;
    top: 45px;
    z-index: -5; }
  .breadcrumb__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 41px;
    color: #eee; }

.catalog__item {
  grid-column: span 3; }

.catalog__img {
  display: block;
  width: 100%;
  object-fit: contain; }

.catalog__title {
  font-weight: 700;
  line-height: 20px;
  margin-bottom: 5px;
  width: 70%; }

.catalog__subtitle {
  font-size: 13px;
  line-height: 16px;
  margin-bottom: 12px; }

.catalog__price {
  font-weight: 700;
  line-height: 20px; }

.catalog__pagination {
  padding: 50px 0; }

.catalog__p._small {
  font-size: 13px;
  color: #666;
  margin-bottom: 5px; }

.catalog__list {
  margin: 0 -3px;
  display: flex;
  align-items: center; }

.catalog__link {
  display: block;
  font-weight: 400;
  color: #666;
  font-size: 16px;
  margin: 0 3px; }
  .catalog__link:hover {
    color: #000; }
  .catalog__link._active {
    font-weight: 700;
    color: #000;
    font-size: 24px; }

.item {
  padding: 70px 0 30px; }
  .item__content {
    grid-column: span 5;
    margin: 0 20px 0 0; }
  .item__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 41px;
    margin-bottom: 8px; }
  .item__subtitle {
    font-size: 13px; }
  .item__form {
    margin: 30px 0;
    padding: 24px 24px 32px 24px;
    background: #F2F2F2; }
    .item__form-title {
      font-family: 'PT Sans', sans-serif;
      font-size: 16px;
      line-height: 20px;
      width: 22%; }
  .item__row {
    display: flex;
    align-items: center;
    margin-bottom: 10px; }
  .item__buttons {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px; }
  .item__button {
    background-color: #FBFBFB;
    border-radius: 8px;
    cursor: pointer;
    padding: 4px 8px;
    border: 1px solid #ddd;
    font-size: 16px;
    font-family: 'PT Sans', sans-serif;
    margin: 5px 10px;
    transition: all .15s ease;
    min-width: 42px;
    text-align: center; }
    .item__button:hover {
      border: 1px solid #666; }
  .item__link {
    display: block;
    margin-top: 10px;
    font-size: 13px;
    text-decoration: underline;
    transition: all .15s ease; }
    .item__link:hover {
      color: #666; }
  .item__button-buy {
    display: block;
    margin-top: 30px;
    font-weight: 700;
    font-size: 16px;
    padding: 12px 25px;
    background: linear-gradient(180deg, #FFC908 0%, #FFBA08 100%);
    box-shadow: 0px 0px 15px rgba(255, 146, 17, 0.5);
    border-radius: 4px;
    border: none;
    cursor: pointer;
    transition: all .15s ease; }
    .item__button-buy:hover {
      background: linear-gradient(180deg, #F5BF00 0%, #F0AD00 100%);
      box-shadow: none; }
  .item__info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 32px; }
  .item__text {
    grid-column: span 2; }
    .item__text._col-1 {
      grid-column: span 1; }
  .item__p._mb {
    margin-bottom: 20px; }
  .item__p._small {
    font-size: 13px;
    font-weight: 700;
    color: #afafaf; }
  .item__images {
    grid-column: span 7; }

.more-items__slide {
  margin: 0 16px; }

/*
Media
*/
@media (max-width: 1200px) {
  .arrows__arrow {
    margin: 0 16px; }
  .header .container {
    padding: 0 16px; }
  .footer .container {
    padding: 0 16px; }
  .mainscreen__slide {
    height: 100vh; }
  .mainscreen__content {
    height: 100%;
    padding: 32px 16px; }
  .mini-catalog__wrapper {
    margin: 0 16px 0 0;
    flex-wrap: wrap;
    justify-content: flex-start; }
  .mini-catalog__info {
    margin: 0 0 0 16px; }
  .mini-catalog__item {
    width: 20%; }
  .grid {
    overflow: hidden; }
    .grid._red .grid__img {
      margin: 0; }
    .grid._pink .grid__img {
      margin-top: -40px; }
    .grid__img {
      width: 100%; }
  .breadcrumb .container {
    margin: 0 16px; }
  .catalog .container {
    margin: 0 16px; }
  .catalog__title {
    width: 100%; } }

@media (max-width: 960px) {
  .container {
    grid-gap: 20px; }
  .footer__item {
    grid-column: span 4; }
  .mini-catalog__info {
    grid-column: span 4; }
  .mini-catalog__wrapper {
    flex-wrap: wrap;
    justify-content: flex-start;
    grid-column: span 8; }
  .mini-catalog__item {
    width: 40%; } }

@media (max-width: 720px) {
  .container {
    grid-gap: 16px; }
  .header {
    background-color: #fff; }
    .header._dark {
      background-color: #000; }
    .header__menu {
      grid-column: span 10; }
    .header__cart {
      grid-column: span 1; }
      .header__cart-span {
        display: none; }
  .footer {
    padding: 30px 0 70px 0; }
    .footer__item {
      grid-column: span 6; }
    .footer__info {
      flex-direction: column; }
    .footer__link_small {
      margin-top: 15px; }
  .mainscreen__title {
    font-size: 48px;
    width: 100%;
    line-height: 62px; }
  .mini-catalog {
    margin-bottom: 0;
    padding-bottom: 0; }
    .mini-catalog__info {
      grid-column: span 12;
      margin: 0 16px; }
    .mini-catalog__wrapper {
      flex-wrap: wrap;
      justify-content: flex-start;
      grid-column: span 12; }
    .mini-catalog__item {
      width: 43%; }
    .mini-catalog .container {
      grid-gap: 0; }
    .mini-catalog .grid {
      grid-column: span 12; }
      .mini-catalog .grid._red .grid__img {
        margin: -10px 0 -30px -30px;
        width: 120%; }
      .mini-catalog .grid._pink .grid__img {
        width: 130%;
        margin: -50px 0; }
  .grid._grey::after {
    background: url("../assets/img/products/shoes.png") 0% 50% no-repeat;
    background-size: cover; }
  .catalog__item {
    grid-column: span 4; } }

@media (max-width: 480px) {
  .footer__item {
    grid-column: span 12; }
  .catalog__item {
    grid-column: span 6; } }

/*
Icons
*/
@font-face {
  font-family: 'icons';
  src: url("../assets/fonts/fontello.woff2?92239550") format("woff2"), url("../assets/fonts/fontello.woff?92239550") format("woff");
  font-weight: normal;
  font-style: normal; }

[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "icons";
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: inline-block; }

.icon-arr:before {
  content: '\e800'; }

.icon-cart:before {
  content: '\e801'; }

.icon-cross:before {
  content: '\e802'; }
